import { css, styled } from '../../utils/styled';
import { inputHeight } from '../../utils/styles';
import { color, radius, spacing, transitionTime, typography } from '../../utils/theme';

export const Container = styled.div`
	position: relative;
	width: 100%;
	display: grid;
	gap: 4px;
`;

export const IconWrapper = styled.div`
	background-color: ${color('background.secondary')};
	width: 36px;
	border-left: 1px solid ${color('border.primary')};
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	right: 0;
	height: 100%;
	top: 0;
	transition: border-color ${transitionTime()}, background-color ${transitionTime()}, color ${transitionTime()};
`;

type DateBoxProps = {
	opened: boolean;
};

export const DateBox = styled.button<DateBoxProps>`
	${typography('description')};
	border: 1px solid ${color('border.primary')};
	height: ${inputHeight}px;
	width: fit-content;
	color: ${color('content.primary')};
	padding: ${spacing(1, 3)};
	padding-right: ${spacing(8)};
	border-radius: ${radius('md')};
	overflow: hidden;
	background-color: ${color('background.primary')};
	position: relative;
	align-items: center;
	text-align: left;
	transition: border-color ${transitionTime()};
	cursor: pointer;

	// condition to avoid hover over writing style whe opened
	${({ opened }) =>
		opened
			? css`
					border-color: ${color('primary.500')};

					${IconWrapper} {
						border-color: ${color('primary.500')};
						background-color: ${color('primary.500')};
						color: ${color('content.white')};
					}
			  `
			: css`
					&:hover {
						border-color: ${color('primary.300')};

						${IconWrapper} {
							border-color: ${color('primary.300')};
							background-color: ${color('primary.200')};
							color: ${color('primary.500')};
						}
					}
			  `}
`;

export const DrawerContent = styled.div`
	margin: ${spacing(4)};
`;

export const Placeholder = styled.span`
	${typography('description')}
	color: ${color('content.secondary')};
`;

export const DateBoxLabel = styled.span`
	padding-right: ${spacing(2)};
	white-space: nowrap;
`;
