import React from 'react';
import type { DayPickerRangeProps, DayPickerSingleProps } from 'react-day-picker';

import { useLocalisation } from '../../utils/hooks';
import { DatePickerNav } from './DatePickerNav';
import { StyledDatePicker } from './styles';

type OmittedProps = 'weekStartsOn' | 'showOutsideDays' | 'defaultMonth' | 'components';
type SelectionModes = 'range' | 'single';

export type BaseDatePickerProps<SelectionMode extends SelectionModes> = SelectionMode extends 'range'
	? Omit<DayPickerRangeProps, OmittedProps>
	: Omit<DayPickerSingleProps, OmittedProps>;

export const BaseDatePicker = <SelectionMode extends SelectionModes>({
	...props
}: BaseDatePickerProps<SelectionMode>) => {
	const { firstDayOfWeek } = useLocalisation();

	const currentMonth = props.mode === 'single' ? props.selected : props.selected?.from;

	return (
		<StyledDatePicker
			weekStartsOn={firstDayOfWeek as DayPickerSingleProps['weekStartsOn']}
			showOutsideDays
			defaultMonth={currentMonth}
			components={{
				Caption: DatePickerNav,
			}}
			{...props}
		/>
	);
};
