import 'react-day-picker/dist/style.css';
import { DayPicker } from 'react-day-picker';

import { styled } from '../../utils/styled';
import { color, fontFamily, radius, typography } from '../../utils/theme';

export const StyledDatePicker = styled(DayPicker)`
	font-family: ${fontFamily('default')};

	&.rdp {
		--rdp-cell-size: 36px;
		--rdp-accent-color: ${color('interactive.primary.idle')}; // selected day
		--rdp-background-color: ${color('surface.primary50')};
		margin: 0;
	}

	// days of the week
	.rdp-head_cell {
		text-transform: capitalize;
		${typography('caption2')};
		color: ${color('content.secondary')};
		cursor: default;
	}

	// days in the currently viewed month
	.rdp-day {
		${typography('captionMedium')};
		border-radius: ${radius('md')};
		color: ${color('content.primary')};
	}

	// days outside the currently viewed month
	.rdp-day_outside {
		color: ${color('content.secondary')};
	}

	// selected day
	.rdp-day_selected:not(.rdp-day_disabled) {
		&:not(.rdp-day_range_middle) {
			color: ${color('content.white')};
		}
		&:hover {
			background-color: ${color('surface.primary50')};
		}
	}

	// today
	.rdp-day_today:not(.rdp-day_selected) {
		color: ${color('interactive.primary.idle')};
	}

	// disabled days
	.rdp-day_disabled,
	.rdp-button[disabled]:not(.rdp-day_selected) {
		background-color: inherit;
		color: ${color('content.disabled')};
		cursor: not-allowed;
		opacity: 1;
	}

	/* Classes for date range selections */

	// days between selected start and end date
	.rdp-day_range_middle {
		background-color: ${color('surface.primary50')};
		color: ${color('content.primary')};
		border-radius: 0;
	}

	// days between selected start and end date, before selecting an end date
	.rdp-range_entered:not(.rdp-day_range_start):not(.rdp-day_range_end) {
		background-color: ${color('surface.primary50')};
		color: ${color('content.primary')};
		border-radius: 0;
	}

	// start date
	.rdp-day_range_start {
		border-radius: ${radius('md')} 0 0 ${radius('md')};
	}

	// end date
	.rdp-day_range_end:not(.rdp-range_first_day) {
		border-radius: 0 ${radius('md')} ${radius('md')} 0;
	}

	// start and end date on the same date
	.rdp-day_range_start.rdp-day_range_end:not(.rdp-range_first_day) {
		border-radius: ${radius('md')};
	}
`;
